
body{
  background-color: #b2aba9;
}
.container{
  background-color: #f2f2f2;
  justify-content: center;
  width: 50%;
  margin-left: 26%;
  margin-top: 5%;
  text-align: center;
}

input[type=text], input[type=password], input[type=email], select {
  width: 80%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

button{
  width: 30%;
  background-color: #4caf50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
button:hover{
  background-color: #45a049;
}
.header{
  width: 95%;
  background-color: rgb(7,2,71);
  padding: 1px 20px;
  color: white;
}

.ui-deine{
  background-color: rgb(243,240,243);
  margin-left: 25%;
  width: 50%;
  padding: 20px 30px;
  margin-top: 5%s;
}
 
